import React from "react"
import Header from "../components/header"
import img1 from "../images/15.png"
import Footer from "../components/Footer"

import "../Style/NewComponentNaprovlenie.css"

export default function Fabrica() {
  return (
    <div>
      <Header />
      <div className="newFlex-blok1" style={{ position: "relative" }}>
          <div>
            <h1 className="ChapterText padding5">
            Швейно-обувная фабрика
            </h1>
            <h2 className="textDescription">
            Мы разрабатываем собственные уникальные модели, инновационные ткани, пропитки, красители, технологии производства.
            </h2>
            <h2 className="textDescription">
            Наша продукция служит дольше и повышает производительность труда. Производственная мощность насчитывает до 300 тысяч комплектов спецодежды в год            </h2>
            <button className="newpositionRel newButton">
              Перейти на сайт
            </button>
          </div>
          <div className="image-overlay"></div>
          <div className="gradientOverlay"></div>
          <img
            className="minHeight"
            src={img1}
            alt={"Мы разрабатываем собственные уникальные модели, инновационные ткани, пропитки, красители, технологии производства."}
          />
        </div>
        <Footer/>
    </div>
  )
}
